<template>
	<div v-if="step == 1" class="sub-container join">
		<h2 class="txt-center marB-50">회원가입</h2>
		<div class="input-box">
			<div class="user-input">
				<input
					type="text"
					ref="userId"
					v-model="id"
					v-on:blur="checkId"
					placeholder="아이디"
					autocomplete="off"
				/>
				<div class="err-msg-container">
					<Exclamation v-if="check_id_msg"></Exclamation>
					<span
						id="check_id"
						v-bind:class="{ redFont: !isValidId, msg: true }"
						>{{ check_id_msg }}</span
					>
				</div>
			</div>
			<div class="user-input">
				<input
					ref="userName"
					type="text"
					v-model="user_name"
					v-on:blur="checkName"
					placeholder="이름"
					autocomplete="off"
				/>
				<div class="err-msg-container">
					<Exclamation v-if="check_name_msg"></Exclamation>
					<span
						id="check_name"
						v-bind:class="{ redFont: !isValidName, msg: true }"
						>{{ check_name_msg }}</span
					>
				</div>
			</div>
			<div class="user-input">
				<input
					type="password"
					ref="userPwd"
					name="pwd"
					v-model="pwd"
					v-on:blur="checkPassword"
					placeholder="비밀번호"
				/>
				<div class="err-msg-container">
					<Exclamation v-if="check_password_msg"></Exclamation>
					<span
						class="msg"
						id="check_pwd"
						v-bind:class="{ redFont: !isValidPassword, msg: true }"
						>{{ check_password_msg }}</span
					>
				</div>
			</div>
			<div class="user-input">
				<input
					ref="pwdConfirm"
					type="password"
					name="pwd-confirm"
					v-model="pwdConfirm"
					v-on:blur="checkPasswordConfirm"
					placeholder="비밀번호 재입력"
				/>
				<div id="cf_pwd"></div>
				<div class="err-msg-container">
					<Exclamation v-if="check_password_confirm_msg"></Exclamation>
					<span
						class="msg"
						id="confirm_pwd"
						v-bind:class="{ redFont: !isValidPasswordConfirm, msg: true }"
						>{{ check_password_confirm_msg }}</span
					>
				</div>
			</div>
			<div class="user-input">
				<input
					ref="userEmail"
					type="text"
					v-model="user_email"
					v-on:blur="checkEmail"
					placeholder="이메일"
					autocomplete="off"
				/>
				<div class="err-msg-container">
					<Exclamation v-if="check_email_msg"></Exclamation>
					<span
						class="msg"
						id="check_email"
						v-bind:class="{ redFont: !isValidEmail, msg: true }"
						>{{ check_email_msg }}</span
					>
				</div>
			</div>
			<div class="user-input">
				<input
					ref="userPhone"
					type="text"
					v-model="user_phone"
					placeholder="전화번호"
					v-on:blur="checkPhone"
					autocomplete="off"
				/>
				<div class="err-msg-container">
					<Exclamation v-if="check_phone_msg"></Exclamation>
					<span
						class="msg"
						id="check_phone"
						v-bind:class="{ redFont: !isValidPhone, msg: true }"
						>{{ check_phone_msg }}</span
					>
				</div>
			</div>
			<div class="user-input zipcode">
				<input
					ref="zipcode"
					type="text"
					placeholder="우편번호"
					v-model="getZipcode"
					@change="checkstreet1"
					@input="checkstreet1"
					autocomplete="off"
					readonly
				/>
				<button
					class="btn dark full"
					type="button"
					@click="execDaumPostcode"
					:checkstreet="checkstreet1"
				>
					검색
				</button>
			</div>
			<div class="user-input">
				<input
					ref="street1"
					type="text"
					placeholder="주소"
					v-model="getstreet1"
					autocomplete="off"
					readonly
				/>
				<div class="err-msg-container">
					<Exclamation v-if="street1_msg"></Exclamation>
					<span
						class="msg"
						id="check_phone"
						v-bind:class="{ redFont: street1_msg, msg: true }"
					>
						{{ street1_msg }}
					</span>
				</div>
			</div>
			<div class="user-input">
				<input
					ref="street2"
					type="text"
					v-model="street2"
					placeholder="상세주소"
					v-on:blur="checkstreet2"
					autocomplete="off"
				/>
				<div class="err-msg-container">
					<Exclamation v-if="street2_msg"></Exclamation>
					<span
						class="msg"
						id="check_phone"
						v-bind:class="{ redFont: !isValidstreet2, msg: true }"
					>
						{{ street2_msg }}
					</span>
				</div>
			</div>
		</div>
		<ul class="join-agreement">
			<li>
				<input
					type="checkbox"
					class="checkbox"
					name="member"
					id="approve_newsletter"
					v-model="approve_newsletter"
				/>
				<label class="marR-5" for="approve_newsletter"></label>
				<span>(선택)뉴스레터 및 광고를 수신하시겠습니까?</span>
			</li>
			<li>
				<input
					type="checkbox"
					class="checkbox"
					name="member"
					id="approve_service_term"
					v-model="approve_service_term"
				/>

				<label
					class="marR-5"
					ref="approve_service_term"
					for="approve_service_term"
				></label>
				<span
					>(필수)<span class="link">서비스 이용약관</span>에 동의합니다</span
				>
			</li>
			<li>
				<input
					type="checkbox"
					class="checkbox"
					name="member"
					id="approve_privacy_term"
					v-model="approve_privacy_term"
				/>
				<label
					class="marR-5"
					ref="approve_privacy_term"
					for="approve_privacy_term"
				></label>
				<span
					>(필수)<span class="link">개인정보 취급 방침</span>에 동의합니다</span
				>
			</li>
		</ul>
		<button
			class="btn full dark"
			id="join_submit"
			type="button"
			@click="openModal"
		>
			확인
		</button>
		<ConfirmModal
			v-if="confirmModal"
			:msg="`입력하신 ${user_email}로 인증번호를 발송합니다.`"
			:single="false"
			@confirm="sendAuthNum"
			@cancel="cancel"
		>
		</ConfirmModal>
		<NotificationModal
			v-if="isModalShown"
			:msg="modalMsg"
			@changeIsShown="changeIsShown"
		></NotificationModal>
	</div>
	<div v-else-if="step == 2" class="sub-container join">
		<h2 class="txt-center marB-50">아이디 및 비멀번호 찾기</h2>

		<div class="auth-info-box">
			<p>
				{{ user_email }}로 전송받은 인증번호를 입력해주시면 회원가입이
				완료됩니다!
			</p>
			<p>인증번호 전송에는 최대 3분정도 소요될 수 있습니다.</p>
		</div>

		<div class="input-box">
			<div class="user-input">
				<input type="text" v-model="authNumber" placeholder="인증번호를 입력" />
			</div>
			<button class="btn full dark" id="login-btn" @click="checkAuthNumber">
				인증하기
			</button>
		</div>
		<LoadingSpinner :loading="isLoading" />
		<NotificationModal
			v-if="isModalShown"
			:msg="modalMsg"
			@changeIsShown="changeIsShown"
		></NotificationModal>
		<ConfirmModal
			v-if="confirmModal"
			:msg="`회원가입이 완료되었습니다`"
			:single="true"
			@confirm="joinComplete"
		>
		</ConfirmModal>
	</div>
</template>

<script>
import {
	validateId,
	validatePassword,
	validatePhone,
	validateName,
	validateEmail,
} from '@/utils/validation';
import { mapGetters } from 'vuex';
import { sendAuthNumber, checkAuthNumber, join } from '@/api/user';
import Store from '@/store';
import Exclamation from '@/assets/icon/Exclamation';
import ConfirmModal from '@/components/common/ConfirmModal.vue';
import NotificationModal from '@/components/common/NotificationModal';
import LoadingSpinner from '@/components/common/LoadingSpinner';

export default {
	name: 'Login',
	components: { LoadingSpinner, Exclamation, ConfirmModal, NotificationModal },
	computed: {
		...mapGetters(['getZipcode', 'getstreet1']),
	},
	created() {
		let address = {};
		Store.commit('SET_ADDRESS', address);
	},
	data() {
		return {
			step: 1,
			id: '',
			pwd: '',
			pwdConfirm: '',
			user_name: '',
			user_email: '',
			user_phone: '',
			isValidId: false,
			check_id_msg: '',
			isValidPassword: false,
			check_password_msg: '',
			isValidPasswordConfirm: false,
			check_password_confirm_msg: '',
			isValidEmail: false,
			check_email_msg: '',
			isValidName: false,
			check_name_msg: '',
			isValidPhone: false,
			check_phone_msg: '',
			street1_msg: '',
			isValidstreet1: false,
			street2: '',
			isLoading: false,
			street2_msg: '',
			isValidstreet2: false,
			approve_newsletter: false,
			approve_service_term: false,
			approve_privacy_term: false,
			confirmModal: false,
			isModalShown: false,
			modalMsg: '',
			authNumber: '',
			authId: '',
		};
	},

	methods: {
		execDaumPostcode() {
			let address = {};
			window.daum.postcode.load(function() {
				new window.daum.Postcode({
					oncomplete: data => {
						// 팝업에서 검색결과 항목을 클릭했을때 실행할 코드를 작성하는 부분.

						// 도로명 주소의 노출 규칙에 따라 주소를 표시한다.
						// 내려오는 변수가 값이 없는 경우엔 공백('')값을 가지므로, 이를 참고하여 분기 한다.
						var roadAddr = data.roadAddress; // 도로명 주소 변수
						address.roadAddress = roadAddr;
						var zipcode = data.zonecode;
						address.zipcode = zipcode;

						// 우편번호와 주소 정보를 해당 필드에 넣는다.
						//document.getElementById('sample4_postcode').value = data.zonecode;
						address.numberAddress = data.jibunAddress;

						Store.commit('SET_ADDRESS', address);
					},
				}).open();
			});
		},
		checkId() {
			const value = this.id;
			if (!value) {
				this.check_id_msg = '아이디를 입력해주세요.';
				this.isValidId = false;
			} else if (!validateId(value)) {
				this.check_id_msg = '영문, 숫자를 사용해주세요';
				this.isValidId = false;
			} else {
				this.check_id_msg = '';
				this.isValidId = true;
			}
		},
		checkPassword() {
			const value = this.pwd;
			if (!value) {
				this.check_password_msg = '비밀번호를 입력해주세요.';
				this.isValidPassword = false;
			} else if (!validatePassword(value)) {
				this.check_password_msg =
					'영문, 숫자,특수문자 필수조합 20자 이하로 입력';
				this.isValidPassword = false;
			} else {
				this.check_password_msg = '';
				this.isValidPassword = true;
			}
		},
		checkEmail() {
			const value = this.user_email;
			if (!value) {
				this.check_email_msg = '이메일을 입력해주세요.';
				this.isValidEmail = false;
			} else if (!validateEmail(value)) {
				this.check_email_msg = '이메일 형식에 맞춰 작성해주세요';
				this.isValidEmail = false;
			} else {
				this.check_email_msg = '';
				this.isValidEmail = true;
			}
		},
		checkName() {
			const value = this.user_name;
			if (!value) {
				this.check_name_msg = '이름을 입력해주세요.';
				this.isValidName = false;
			} else if (!validateName(value)) {
				this.check_name_msg = '8자리로 이하로 입력해주세요.';
				this.isValidName = false;
			} else {
				this.check_name_msg = '';
				this.isValidName = true;
			}
		},
		checkPhone() {
			const value = this.user_phone;
			if (!value) {
				this.check_phone_msg = '휴대폰 번호를 입력해주세요.';
				this.isValidPhone = false;
			} else if (!validatePhone(value)) {
				this.check_phone_msg = '연락처 양식에 맞게 작성해주세요';
				this.isValidPhone = false;
			} else {
				this.check_phone_msg = '';
				this.isValidPhone = true;
			}
		},
		checkPasswordConfirm() {
			const value = this.pwdConfirm;
			if (!value) {
				this.check_password_confirm_msg =
					'비밀번호 확인을 위해 한번 더 입력해주세요';
				this.isValidPasswordConfirm = false;
			} else if (!this.isPasswordSame()) {
				this.check_password_confirm_msg = '비밀번호가 일치하지 않습니다.';
				this.isValidPasswordConfirm = false;
			} else {
				this.check_password_confirm_msg = '';
				this.isValidPasswordConfirm = true;
			}
		},
		isPasswordSame() {
			if (this.pwd === this.pwdConfirm) {
				return true;
			} else {
				return false;
			}
		},
		checkstreet1() {
			const value = this.getstreet;
			if (!value) {
				this.street1_msg = '주소를 입력해주세요';
				this.isValidstreet1 = false;
			} else {
				this.street1_msg = '';
				this.isValidstreet1 = true;
			}
		},
		checkstreet2() {
			const value = this.street2;
			if (!value) {
				this.street2_msg = '상세주소를 입력해주세요';
				this.isValidstreet2 = false;
			} else {
				this.street2_msg = '';
				this.isValidstreet2 = true;
			}
		},
		openModal() {
			if (!this.isValidId) {
				this.checkId();
				return false;
			}

			if (!this.isValidName) {
				this.checkName();
				return false;
			}

			if (!this.isValidPassword) {
				this.checkPassword();
				return false;
			}

			if (!this.isValidPasswordConfirm) {
				this.checkPasswordConfirm();
				return false;
			}

			if (!this.isValidEmail) {
				this.checkEmail();
				return false;
			}

			if (!this.isValidPhone) {
				this.checkPhone();
				return false;
			}

			if (!this.getstreet1) {
				this.modalMsg = '주소를 입력해주세요';
				console.log(this.isModalShown);
				this.changeIsShown(true);
				console.log(this.isModalShown);
				return false;
			}

			if (!this.isValidstreet2) {
				this.checkstreet2();
				return false;
			}

			if (!this.approve_service_term || !this.approve_privacy_term) {
				this.$refs.approve_service_term.classList.add('unchecked');
				this.$refs.approve_privacy_term.classList.add('unchecked');

				return false;
			}

			this.confirmModal = true;
			this.selectedUser = null;
		},
		cancel() {
			this.confirmModal = false;
			this.selectedUser = null;
		},
		async sendAuthNum() {
			let user = {
				email: this.user_email,
			};
			this.isLoading = true;
			try {
				const { data } = await sendAuthNumber(user);
				if (data.successYn == 'Y') {
					this.authId = data.authId;
					this.step = 2;
				} else if (data.successYn == 'N') {
					this.modalMsg = '이미 존재하는 이메일입니다';
					this.changeIsShown(true);
				}
			} catch (e) {
				this.modalMsg = '인증번호 전송에 실패했습니다. 다시 시도해주세요';
				this.changeIsShown(true);
			} finally {
				this.isLoading = false;
			}

			this.confirmModal = false;
		},
		async checkAuthNumber() {
			this.isLoading = true;
			try {
				let authData = {
					id: this.authId,
					email: this.user_email,
					authNum: this.authNumber,
				};
				let { data } = await checkAuthNumber(authData);
				if (data.result == 'Y') {
					let userData = {
						identity: this.id,
						email: this.user_email,
						password: this.pwd,
						name: this.user_name,
						phone: this.user_phone,
						zipcode: this.getZipcode,
						street1: this.getstreet1,
						street2: this.street2,
						agreeYn: this.approve_newsletter ? 'Y' : 'N',
					};
					try {
						let { data } = await join(userData);
						console.log(data);
						if (data.successYn == 'Y') {
							this.modalMsg = '회원가입이 완료되었습니다!';
							this.changeIsShown(true);
						} else if (data.successYn == 'N') {
							this.modalMsg = '회원가입에 실패했습니다!';
							this.changeIsShown(true);
						}
					} catch (e) {
						console.log(e);
						this.modalMsg = '회원가입에 실패했습니다!';
						this.changeIsShown(true);
					} finally {
						this.isLoading = false;
					}
				} else {
					this.modalMsg = '인증번호가 일치하지 않습니다';
					this.changeIsShown(true);
				}
			} catch (e) {
				console.log(e);
			} finally {
				this.isLoading = false;
			}
		},
		async join() {
			let userData = {
				identity: this.id,
				email: this.user_email,
				password: this.pwd,
				name: this.user_name,
				phone: this.user_phone,
				zipcode: this.getZipcode,
				street1: this.getstreet1,
				street2: this.street2,
				agreeYn: this.approve_newsletter,
			};

			console.log(userData);
			this.isLoading = true;
			try {
				let { data } = await join(userData);
				console.log(data);
				if (data.successYn == 'Y') {
					this.modalMsg = '회원가입이 완료되었습니다!';
					this.changeIsShown(true);
				}
			} catch (e) {
				console.log(e);
				this.modalMsg = '회원가입에 실패했습니다!';
				this.changeIsShown(true);
			} finally {
				this.isLoading = false;
			}
		},
		joinComplete() {
			this.confirmModal = false;
			this.$router.push('/');
		},
		changeIsShown(isShown) {
			this.isModalShown = isShown;
		},
	},
};
</script>

<style lang="scss" scoped>
.user-input.zipcode {
	display: flex;
	padding: 0;
	.btn {
		box-sizing: border-box;
		height: 55px;
		width: 50%;
	}
}

.auth-info-box {
	margin: 50px auto 30px;
}
</style>
